



















import Vue from 'vue';

export default Vue.extend({
	data(): any {
		return {};
	},
	props: {
		name: String,
		id: Number,
		sections: Array
	},
	methods: {
		sectionUrl(section): string {
			switch (section.name) {
				case 'countries':
					return '/Icons/countries.png';
				case 'ideaGroups':
					return '/Icons/ideas.png';
				case 'policies':
					return '/Icons/policies.png';
				case 'religionGroups':
					return '/Icons/religions.png';
			}
			return "";
		}
	}
});
